	module.exports = {"data":{"talkJson":{"description":"j2me.js is a Java VM written in pure JavaScript that is geared towards modern Web-enabled mobile devices such as Firefox OS. Its designed to run Java/MIDP games and apps in an HTML5 environment, enabling old feature phone content to live on in the modern smartphone world. My talk will in focus in particular on mapping the more complex language features of the Java VM onto JavaScript and HTML5, including multithreading and synchronous I/O.\n\nhttps://www.jsconfar.com/","meta":{"duration":"PT44M10S"},"presenter":{"name":null},"slug":"andreas-gal-j2mejs-a-multi-threaded-java-vm-in-javascript-or-jsconfar-2014","source":{"id":"I7hZ8I9G5KA","provider":"youtube"},"tag":[],"title":"Andreas Gal: j2me.js - a multi-threaded Java VM in JavaScript | JSConf.ar 2014"},"allTalkJson":null},"pathContext":{"slug":"andreas-gal-j2mejs-a-multi-threaded-java-vm-in-javascript-or-jsconfar-2014","tags":[]}}

